const uni = require('uni').default;




















export default {
	data() {
		return {
			showClearIcon: false,
			inputClearValue: ""
		}
	},
	onShareAppMessage() {
		return {
			title: '欢迎体验uni-app',
			path: '/pages/API/API'
		}
	},
	methods: {
		goDetailPage(e) {
			let url = ~e.indexOf('platform') ? e : '/pages/API/' + e + '/' + e;
			uni.navigateTo({
				url: url
			})
		},
		bindClearInput: function (e) {
			this.inputClearValue = e.target.value;
			if (e.target.value.length > 0) {
				this.showClearIcon = true;
			} else {
				this.showClearIcon = false;
			}
		},
		clearIcon: function () {
			this.inputClearValue = "";
			this.showClearIcon = false;
		}

	}
}



